.work{
    padding: 0.3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
 
.w-right{
    position: relative;
}
.w-secCircle>img {
    height: 4rem;
    width: 4rem;

}
.w-mainCircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background:white;
}
.w-mainCircle>*{ position: absolute;}
.w-mainCircle> :nth-child(1){
    top: -3rem;
    left: 6.5rem;
    
}
.w-mainCircle> :nth-child(2){
    top: 6rem;
    left: -2.5rem;

}
.w-mainCircle> :nth-child(3){
    top: 6rem;
    left: 15rem;

}
.w-mainCircle> :nth-child(4){
    top: 15rem;
    left: 6.5rem;

}
.w-mainCircle> :nth-child(5){
    top: 6rem;
    left: 6.5rem;

}
.w-secCircle {
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
}

.w-backCircle{
position: absolute;
height: 13rem;
width: 13rem;
border-radius: 100%;
z-index: -1;
}
.blueCircle{
    background: #1949B8;
    left: 20rem;
    top : 0rem

}
.yrllowCircle{
    background: #F5C32C;
    left: 20rem;
    top : 9rem

}
 @media screen and (max-width : 480px){
    .work{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        gap: 5rem;
      
     
    }
    .w-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.7);
    }
    .w-mainCircle{
        position: static;

    }
    .w-backCircle{
        left: 8rem !important;
    }
  
 }